<template>
  <div class="main-div">
    <div class="logo"></div>
    <div class="description-container">
      <p id="question">週末到了，你想從基隆出發到淡水走走，眼見時間已經13:00了，你想在14:30前抵達淡水捷運站，請問要如何搭乘？</p>
    </div>
    <div>
      <p id="answer-title">輸入轉乘班次代碼</p>
    </div>
    <div id="answer">
      <input type="text" class="inputbox" v-model="answer1">
      <img id="arrow" src="@/assets/images/commute/arrow-right.png" alt="">
      <input type="text" class="inputbox" v-model="answer2">
    </div>
    <div id="button" @click="submitPressed"></div>
    <Correct v-if="showCorrectMessage" />
    <Wrong v-if="showWrongMessage" />
  </div>
</template>

<script>
import Correct from '@/components/TransferCorrect'
import Wrong from '@/components/TransferWrong'

export default {
  name: 'TransferDiagramTwo',
  components: {
    Correct,
    Wrong
  },
  data () {
    return {
      window: {
        height: window.innerHeight,
        width: window.innerWidth
      },
      showCorrectMessage: false,
      showWrongMessage: false,
      answer1: '',
      answer2: ''
    }
  },
  mounted () {
    this.$store.commit('changeHeader', false)
    this.$store.commit('showFooter', false)
  },
  methods: {
    submitPressed () {
      const lowerCaseAnswer1 = this.answer1.trim().toLowerCase()
      const lowerCaseAnswer2 = this.answer2.trim().toLowerCase()
      if ((lowerCaseAnswer1 === 'tr13' && lowerCaseAnswer2 === 'tp13') || (lowerCaseAnswer1 === 'ksh13' && lowerCaseAnswer2 === 'tp13') || (lowerCaseAnswer1 === 'kt13' && lowerCaseAnswer2 === 'tp13')) {
        console.log('correct')
        this.showCorrectMessage = true
        setTimeout(this.gotoNextQuestion, 1500)
      } else {
        console.log('wrong')
        this.showWrongMessage = true
        setTimeout(this.removeWrongMessage, 1000)
      }
    },
    gotoNextQuestion () {
      this.$router.push({name: 'CommuteTransferDiagramThree'})
    },
    removeWrongMessage () {
      this.showWrongMessage = false
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    min-height: 100vh;
    width: 100%;
    background-color: black;
    text-align: center;
    position: relative;
    .logo{
      @extend centerContainImage;
      margin-top: 15%;
      display: inline-block;
      width: 200px;
      height: 200px;
      background-image: url('~@/assets/images/commute/transfer-diagram2.png');
    }
    #question {
      @extend transferQuestionGreenText;
    }

    #answer-title {
      @extend transferAnswerTitleWhiteText;
    }

    #answer {
      .inputbox {
        width: 80px;
      }
      #arrow {
        width: 15px;
        height: 15px;
      }
    }

    #button {
      display: inline-block;
      margin-top: 15px;
      width: 45px;
      height: 45px;
      background-image: url('~@/assets/images/go-right-arrow.png');
      background-size: contain;
    }
  }
</style>



